<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Withdraw</h1>
      <div class="chartWrap">
        <van-icon @click="$router.push('addBankcard')" name="add-o" />
      </div>
    </section>
    <div class="amount">
      <ul>
        <li>
          <div class="coat" @click="show = true">
            <div class="label">{{ defaultCard.cardNum }}</div>
            <div class="cardNumber"></div>
            <span class="iconfont gray"> &#xe671; </span>
          </div>
        </li>
        <li>
          <div class="amountWrap">
            <p>Withdrawal amount</p>
            <div class="amountMoney"><van-field type="number"  v-model="amount" /> AED</div>
            <span class="surplus "
              >Cash Left <i style="color: #E33946;">{{ cash }}</i> AED</span
            >
          </div>
        </li>
        <li>
          <div class="accountPwd">
            <div class="label">Account Password</div>
            <van-field
              v-model="password"
              :type="passwordType"
              @click-right-icon="
                passwordType = passwordType == 'password' ? 'text' : 'password'
              "
              right-icon="eye-o"
            />
          </div>
        </li>
      </ul>
    </div>
    <div class="withdraw">
      <p @click="$router.push('resetPwd')">Forget Password?</p>
      <van-button type="danger" @click="submitWithdraw">Withdraw</van-button>
      <span class="tips">
        Depending on the situation of different banks, the time of transfer may
        be different. But the time is normally within 72 hours. Please pay
        attention to the notification or bank text messages. If you have any
        questions, please contact customer service :
        <i style="color: #1d79e1;">400-820-888</i>
      </span>
    </div>
    <van-popup v-model="show" position="bottom" :overlay="true">
      <div class="newCard">
        <span @click="show = false">Cancel</span>
        <span class="add" @click="redirect('/myBankcards')">Add New Card</span>
      </div>
      <div class="choiceBankWrap">
        <div class="choiceBank" v-for="(item, index) in cardList" :key="index">
          <div class="btn_left">
            <van-radio-group v-model="selectBank">
              <van-radio :name="index" checked-color="#e33946"></van-radio>
            </van-radio-group>
          </div>
          <div class="bankName">
            <p>{{ item.name }}</p>
            <span>{{ item.cardNum }}</span>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { withdrawCashPro, withdrawCash } from "@/api/store";
export default {
  name: "withdraw",
  data() {
    return {
      radio: "1",
      show: false,
      cardList: [],
      cash: 0,
      cardId: 0,
      selectBank: 0,
      defaultCard:{},
      password: "",
      passwordType: "password",
      amount: 0
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      withdrawCashPro().then(res => {
        this.cardList = res.data.result.userBankCards;
        this.defaultCard = this.cardList[0];
        this.cash = res.data.result.cash;
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    submitWithdraw() {
      withdrawCash({
        cardId: this.defaultCard.id,
        cash: this.amount,
        password: this.password
      }).then(res => {
        this.$toast("Withdraw applying successfully");
        this.$router.go(-1);
      });
    }
  },
  watch: {
    selectBank: function(val) {
      this.defaultCard = this.cardList[val];
      this.show = false;
    },
    amount:function(val){
        if(val>this.cash){
            this.amount=this.cash;
        }
    }
  }
};
</script>

<style lang="scss" scoped>
.van-icon {
  font-size: 20px;
  color: #e33946;
}
.amount {
  li {
    border-top: 10px solid #f6f6f6;
    .amountWrap {
      padding: 0 4%;
      p {
        font-size: 13px;
        color: #666666;
        padding: 14px 0 0 0;
      }
      .surplus {
        font-size: 12px;
        padding-bottom: 0px;
        font-weight: 400;
        color: #999999;
        border: none;
        padding: 2px 0;
      }
      .amountMoney /deep/ {
        width: 100%;
        display: flex;
        align-items: center;
        font-size: 20px;
        .van-cell {
          width: 25%;
          padding-left: 0;
          padding-right: 0;
          .van-field__body {
            font-size: 20px;
          }
        }
      }
    }
  }
}

.coat {
  width: 100%;
  display: -webkit-box;
  padding: 0 4%;
  height: 48px;
  line-height: 48px;
  .label {
    color: #666666;
    width: 25%;
    display: flex;
    align-items: center;
    font-size: 14px;
  }
  .cardNumber {
    width: 70%;
  }
}
.accountPwd {
  width: 100%;
  display: -webkit-box;
  padding: 0 4%;
  .label {
    color: #666666;
    width: 40%;
    display: flex;
    align-items: center;
    font-size: 14px;
  }
  .codeName {
    display: flex;
    align-items: center;
    padding-right: 10px;
  }
  .van-cell {
    width: 60%;
    padding-right: 0;
  }
}
.withdraw {
  width: 100%;
  padding: 0 4%;
  p {
    font-size: 11px;
    color: #1d79e1;
    text-align: right;
    padding: 5px 0 80px 0;
  }
  .van-button {
    width: 100%;
  }
  .tips {
    padding-top: 10px;
    font-size: 10px;
    color: #666666;
    -webkit-box-orient: vertical;
    word-break: break-all;
    display: block;
  }
}
.maskPlate {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  z-index: 100;
}
.newCard {
  padding: 20px 4% 0px;
  display: -webkit-box;
  span {
    color: #999999;
    font-size: 15px;
    display: block;
    width: 50%;
  }
  .add {
    color: #e33946;
    text-align: right;
  }
}
.choiceBankWrap {
  padding: 20px 0 20px 4%;
  width: 100%;
  .choiceBank {
    display: -webkit-box;
    width: 100%;
    .btn_left {
      display: flex;
      align-items: center;
      padding-right: 15px;
    }
    .bankName {
      padding: 10px 0 15px 0;
      p {
        font-size: 15px;
        padding-bottom: 10px;
      }
      span {
        color: #666666;
        font-size: 14px;
      }
    }
  }
}
</style>



